.Burger{
    width: 100%;
    margin:auto;
    overflow-y: scroll;
    text-align: center;
    font-weight: bold;
    height: 250px;
    font-size: 1.2rem;
}

@media (min-width: 1000px) and (min-height:700px){
    .Burger{
        width: 700px;
        height: 600px  ;
    }
}
